import LineGradient from "../components/LineGradient";
import { motion } from "framer-motion";
import useMediaQuery from "../hooks/useMediaQuery";

const MySkills = () => {
  const isAboveMediumScreens = useMediaQuery("(min-width: 1060px)");
  return (
    <section id="skills" className="pt-10 pb-24">

      {/* -----HEADER & IMAGE SECTION----- */}

      <div className="md:flex md:justify-between md:gap-16 mt-32">
        <motion.div
          className="md:w-1/3"
          initial="hidden"
          whileInView="visible"
          viewport={{ once:true, amount:0.5 }}
          transition={{ delay:0.4, duration:0.5 }}
          variants={{
            hidden: { opacity:0, x:-50 },
            visible: { opacity:1, x:0 }
          }}
        >
          <p className="font-playfair font-semibold text-4xl mb-5">
            MY <span className="bg-red">SKILLS</span>
          </p>
          <LineGradient width="w-1/3" />
          <p className="mt-10 mb-7">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. 
          </p>
        </motion.div>

        <div className="mt-16 md:mt-0">
          {isAboveMediumScreens ? 
            (
              <div className="relative z-0 ml-20 before:absolute before:-top-10 before:-left-10  before:w-full before:h-full before:border-2 before:border-blue before:z-[-1]">
                <img 
                  alt="skills"
                  className="z-10"
                  src="../assets/skills-image.png"
                />
              </div>
            ) : 
            (
              <img 
                  alt="skills"
                  className="z-10"
                  src="../assets/skills-image.png"
              />
            )
          }
        </div>
      </div>

      {/* -----SKILLS----- */}
      <div className="md:flex md:justify-between md:gap-16 mt-24">
          {/* -----EXPERIENCE----- */}
          <motion.div 
            className="md:w-1/3"
            initial="hidden"
            whileInView="visible"
            viewport={{ once:true, amount:0.5 }}
            transition={{ duration:0.5 }}
            variants={{
              hidden: { opacity:0, y:50 },
              visible: { opacity:1, y:0 }
            }}
          >
            <div className="relative h-28">
              <div className="z-10">
                <p className="font-playfair font-semibold text-4xl">01</p>
                <p className="font-playfair font-semibold text-2xl mt-2">Experience</p>
              </div>
              <div className="w-1/2 md:w-3/4 h-28 bg-blue absolute top-0 right-0 z-[-1]"></div>
            </div>
            <p className="mt-3">
              Doloribus, reprehenderit assumenda temporibus ab nihil fugiat dolorum quas voluptatum placeat debitis, quasi facere vero.
            </p>
          </motion.div>

          {/* -----INNOVATIVE----- */}
          <motion.div 
            className="md:w-1/3 mt-7"
            initial="hidden"
            whileInView="visible"
            viewport={{ once:true, amount:0.5 }}
            transition={{ delay:0.2, duration:0.5 }}
            variants={{
              hidden: { opacity:0, y:50 },
              visible: { opacity:1, y:0 }
            }}
          >
            <div className="relative h-28">
              <div className="z-10">
                <p className="font-playfair font-semibold text-4xl">02</p>
                <p className="font-playfair font-semibold text-2xl mt-2">Innovative</p>
              </div>
              <div className="w-1/2 md:w-3/4 h-28 bg-red absolute top-0 right-0 z-[-1]"></div>
            </div>
            <p className="mt-3">
              Doloribus, reprehenderit assumenda temporibus ab nihil fugiat dolorum quas voluptatum placeat debitis, quasi facere vero.
            </p>
          </motion.div>

          {/* -----IMAGINATIVE----- */}
          <motion.div 
            className="md:w-1/3 mt-7"
            initial="hidden"
            whileInView="visible"
            viewport={{ once:true, amount:0.5 }}
            transition={{ delay:0.2, duration:0.5 }}
            variants={{
              hidden: { opacity:0, y:50 },
              visible: { opacity:1, y:0 }
            }}
          >
            <div className="relative h-28">
              <div className="z-10">
                <p className="font-playfair font-semibold text-4xl">03</p>
                <p className="font-playfair font-semibold text-2xl mt-2">Imaginative</p>
              </div>
              <div className="w-1/2 md:w-3/4 h-28 bg-yellow absolute top-0 right-0 z-[-1]"></div>
            </div>
            <p className="mt-3">
              Doloribus, reprehenderit assumenda temporibus ab nihil fugiat dolorum quas voluptatum placeat debitis, quasi facere vero.
            </p>
          </motion.div>
          </div>
    </section>
  )
}

export default MySkills