import LineGradient from "../components/LineGradient"
import { motion } from "framer-motion"

const Testimonials = () => {
  const testimonials = `mx-auto relative max-w-[300px] h-[250px] flex flex-col justify-end p-12 mt-48
                        before:absolute before:top-[-120px] before:-ml-[110px] before:left-1/2`;

  return (
    <section id="testimonials" className="pt-32 pb-16">
      {/* ---HEADING--- */}
      <motion.div
          className="md:w-1/3 text-center md:text-left"
          initial="hidden"
          whileInView="visible"
          viewport={{ once:true, amount:0.5 }}
          transition={{ delay:0.4, duration:0.5 }}
          variants={{
            hidden: { opacity:0, x:-50 },
            visible: { opacity:1, x:0 }
          }}
        >
          <p className="font-playfair font-semibold text-4xl mb-5 text-red">
            TESTIMONIALS
          </p>
          <LineGradient width="mx-auto w-2/4" />
          <p className="mt-10">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. 
          </p>
        </motion.div>

        {/* ---TESTIMONIALS--- */}
        <div className="md:flex md:justify-between gap-6">
          <motion.div
            className={`bg-blue ${testimonials} before:content-person1`}
            initial="hidden"
            whileInView="visible"
            viewport={{ once:true, amount:0.5 }}
            transition={{ duration:0.5 }}
            variants={{
              hidden: { opacity:0, scale:0.8 },
              visible: { opacity:1, scale:1}
            }}
          >
            <p className="font-playfair text-4xl">"</p>
            <p className="text-center text-sm ">
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
            </p>
          </motion.div>

          <motion.div
            className={`bg-red ${testimonials} before:content-person2`}
            initial="hidden"
            whileInView="visible"
            viewport={{ once:true, amount:0.5 }}
            transition={{ delay:0.2, duration:0.5 }}
            variants={{
              hidden: { opacity:0, scale:0.8 },
              visible: { opacity:1, scale:1}
            }}
          >
            <p className="font-playfair text-4xl">"</p>
            <p className="text-center text-sm">
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
            </p>
          </motion.div>

          <motion.div
            className={`bg-yellow ${testimonials} before:content-person3`}
            initial="hidden"
            whileInView="visible"
            viewport={{ once:true, amount:0.5 }}
            transition={{ delay:0.4, duration:0.5 }}
            variants={{
              hidden: { opacity:0, scale:0.8 },
              visible: { opacity:1, scale:1}
            }}
          >
            <p className="font-playfair text-4xl">"</p>
            <p className="text-center text-sm">
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
            </p>
          </motion.div>
        </div>
    </section>
  )
}

export default Testimonials